.contact{
    color: #fff;
    font-family: 'poppins', sans-serif ;
}

.contactList{
    display: flex;
    justify-content: space-around;
}

.contactListItem{
    display: flex;
    align-items: center;
    gap: 1rem;
}

.socialmedia{
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}

.contactListItem a{
    text-decoration: none;
    color: #fff;
}

.contactListItem a:hover{
    color: #0ef;
}