.section{
    margin-top:10rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    color: #fff;
    font-family: 'poppins', sans-serif ;
}

.sectionContent h3{
    font-size: 2rem;
    font-weight: 700;
}

.sectionContent h1{
    font-size: 3.5rem;
    font-weight: 700;
    line-height: 1;
}

.sectionContent p{
    font-size: 1.2rem;
}

.sectionImg{
    width: 25rem;
    height: 25rem;
}

.aboutSection{
    margin-top: 5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    color: #fff;
    font-family: 'poppins', sans-serif ;
}

.sectionImgWrapper{
    min-width: 40rem;
    align-self: center;
}

.aboutContent{
    width: 50%;
}

.sectionName{
    font-size: 2.5rem;
    font-weight: 700;
}

.aboutHeading{
    font-size: 3.5rem;
}

.aboutContent h2{
     line-height: 1.2;
}

.aboutContent h3{
    font-size: 2.6rem;
}

@media only screen and (max-width: 1100px) {
    .section{
        flex-direction: column-reverse;
    }
    .aboutSection{
        flex-direction: column;
        justify-content: flex-start;
    }
    .sectionImgWrapper{
        width: 100%;
    }
    .sectionImg{
        width: 100%;
        object-fit: contain;
    }
    .aboutContent{
        width: 100%;
    }
    .aboutHeading{
        text-align: center;
    }
  }