.skill{
    color: #fff;
    font-family: 'poppins', sans-serif ;
}

.skillsWrapper{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.programs{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 1rem;
}

.skilsImg{
    width: 2rem;
    height: 2rem;
}

.imgSkillWrapper{
    display: flex;
    align-items: center;
    gap: 1rem;
}

@media only screen and (max-width: 600px) {
    .skillsWrapper{
        flex-direction: column;
        align-items: center;
    }
}