.header{
    position: fixed;
    top:0;
    left:0;
    width: 90%;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-family: 'poppins', sans-serif ;
    flex-wrap: wrap;
    background-color: #1d212a;
}

.logo{
    font-size: 1.5rem;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    width: 40%;
    text-align: center;
}

.navbar{
    width:60%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.navLink{
    font-size: 1rem;
    color: #fff;
    
}

.navLink:hover{
    color:#0ef;
    cursor: pointer;
}

